import React from "react";
import Data from "./Newdata";
import {Carousel} from 'react-bootstrap';

const Newsliderbanner = () =>{
    return(
        <>
        <Carousel>
            {Data.Sliderdata.map((sval) =>
            <Carousel.Item>
            <div className="slider-text" data-aos="fade-right" data-aos-delay="800">
            <h2>{sval.sltitle}</h2>
            <p>{sval.sltext}</p>
            </div>
            <img className="d-block w-100 img-big" src={sval.deskbigimg} alt="First slide"/>
            <img className="d-block img-sm" src={sval.desksmimg} alt="First slide"/>
            </Carousel.Item>
            )
            } 
        </Carousel>
        </>
    )
}

export default Newsliderbanner;