import React from "react";
import Services from "./Services";
import Data from "./Newdata";

const HomeOurServices = () => {
    return (
    <>
    <section className="our-services">
        <div className="container">
            <h3 data-aos="fade-down" data-aos-delay="500">Our Services</h3>
            <div className="row">
                {Data.Ourservice.map((sval) =>
                    <Services img={sval.sericon} title={sval.serttl} serparagr={sval.serpara} links={sval.serlink} />
                    )
                }
            </div>
        </div>
    </section>
    </>
    )
}
// console.log(Ourservice);
export default HomeOurServices;