import React from "react";

const Services = (props) => {
    return (
    <>
    <div className="col-lg-3 col-md-6 col-12" data-aos="fade-left" data-aos-delay="500">
    <img src={props.img} alt=""/> <h2>{props.title}</h2>
    <p>{props.serparagr}</p>
    <a href={props.links}>Read More...</a>
    </div>
    </>
    )}
// console.log(Services);
    export default Services;
                