import React from "react";
import {NavLink} from "react-router-dom"

const Navigation = () => {

    return(
    <>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <div className="container">
                <a className="navbar-brand" href="#"><img src="/images/logo.png" /></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                        <li className="nav-item"><NavLink className="nav-link" activeClassName="active" exact aria-current="page" to="/">Home</NavLink></li>
                        <li className="nav-item"><NavLink className="nav-link" activeClassName="active" to="/companyProfile">Company Profile</NavLink></li>
                        <li className="nav-item"><NavLink className="nav-link" activeClassName="active" to="/ourServices">Our Services</NavLink></li>
                        <li className="nav-item"><NavLink className="nav-link" activeClassName="active" to="/career">Career</NavLink></li>
                        <li className="nav-item"><NavLink className="nav-link" activeClassName="active" to="/contactUs">Contact Us</NavLink></li>
                    </ul>
                </div>
            </div>
        </nav>

    </>
    )
}

export default Navigation;