import React from "react";

const HomeCounter = () => {
    return (
    <>
        <section className="counter h-50">
            <div className="container">
                <div className="row">
                <div className="col-12 d-flex">
                <div data-aos="fade-down" data-aos-delay="500"><img src="images/counter-logo.png" alt=""/></div>
                <div data-aos="fade-down" data-aos-delay="500">
                    <h2 className="yellow">Lorem Ips aliquip</h2>
                    <h2 className="blue">Ut enim ad minim veniam</h2>
                </div>
                </div>
                <ul data-aos="zoom-in" data-aos-delay="500">
                    <li><h2 className="count">100</h2><p>Lorem dolor</p></li>
                    <li><h2 className="count">800</h2><p>Lorem dolor</p></li>
                    <li><h2 className="count">750</h2><p>Lorem dolor</p></li>
                    <li><h2 className="count">300</h2><p>Lorem dolor</p></li>
                </ul>
                </div>
            </div>
        </section>
    </>
    )
}

