import React from "react";

import {Carousel} from 'react-bootstrap';
import Data from "./Newdata";

const HomeTesti = () => {
    return (
    <>
        <section className="testimonials">
        <div className="container">
            <h3 data-aos="fade-down" data-aos-delay="500">Testimonials</h3>
            <div className="row">
            <Carousel>
            {Data.Testimoni.map((sval) =>
            <Carousel.Item>
                <ul className="d-block w-100">
                    <li>
                    <div className="row">
                    <div className="col-lg-6 col-md-6 col-12 content" data-aos="zoom-in" data-aos-delay="500">
                    <div className="comma">&quot;</div>
                    <div className="testi-content"><img src={sval.testimg} alt=""/><p>{sval.conten}</p><span className="testi-name">- {sval.name}</span></div>
                    <div className="testi-bg-clip"><div className="testi-bg-box"></div></div>
                    </div>

                    <div className="col-lg-6 col-md-6 col-12 content" data-aos="zoom-in" data-aos-delay="500">
                    <div className="comma">&quot;</div>
                    <div className="testi-content"><img src={sval.testimg1} alt=""/><p>{sval.conten1}</p><span className="testi-name">- {sval.name1}</span></div>
                    <div className="testi-bg-clip"><div className="testi-bg-box"></div></div>
                    </div>
                    </div>
                    </li>
                </ul>                 
            </Carousel.Item>
            )}
            </Carousel>
            </div>
            </div>
    </section>
    </>
    )
}

export default HomeTesti;