import React from "react";
import Data from "./Newdata";

const HomeWhyUs = () => {
    return (
    <>
    {Data.whyus.map((sval) =>
    <section>
        <div className="container">
        <h3 data-aos="fade-down" data-aos-delay="500">{sval.whyttl}</h3>
        <p data-aos="fade-right" data-aos-delay="500">{sval.whypara}</p>
        </div>
    </section>
    )
}
    </>
    )
}

export default HomeWhyUs;